import React from 'react';
import { Line } from 'react-chartjs-2';

const Results = ({ inputValues }) => {
  const calculateResults = () => {
    const { initialInvestment, operationalCosts, revenueStreams, totalInitialInvestment } = inputValues;

    const parseValue = (value) => parseFloat(value) || 0;

    const totalOperationalCosts = Object.values(operationalCosts).reduce((a, b) => parseValue(a) + parseValue(b), 0);
    let totalRevenue = parseValue(revenueStreams.individualSessionPrice) * parseValue(revenueStreams.individualSessionCustomers);

    if (revenueStreams.subscriptionsEnabled) {
      totalRevenue += parseValue(revenueStreams.subscriptionPrice) * parseValue(revenueStreams.subscriptionCustomers) +
        parseValue(revenueStreams.subscriptionSessionCost) * parseValue(revenueStreams.subscriptionSessions) * parseValue(revenueStreams.subscriptionCustomers);
    }

    if (revenueStreams.contractsEnabled) {
      totalRevenue += parseValue(revenueStreams.contractPrice) * parseValue(revenueStreams.contractCustomers);
    }

    const monthlyProfit = totalRevenue - totalOperationalCosts;
    const breakEvenMonths = totalInitialInvestment / monthlyProfit;
    const breakEvenWeeks = breakEvenMonths * 4.33;

    const calculateCustomersNeeded = (monthlyCosts, revenuePerCustomer) => {
      if (revenuePerCustomer > 0) {
        return monthlyCosts / revenuePerCustomer;
      }
      return 0;
    };

    const individualSessionCustomersNeeded = calculateCustomersNeeded(totalOperationalCosts, parseValue(revenueStreams.individualSessionPrice));
    const subscriptionCustomersNeeded = calculateCustomersNeeded(totalOperationalCosts, parseValue(revenueStreams.subscriptionPrice));
    const contractCustomersNeeded = calculateCustomersNeeded(totalOperationalCosts, parseValue(revenueStreams.contractPrice));

    return {
      labels: ['Week 1', 'Week 2', 'Week 3', 'Week 4'],
      datasets: [
        {
          label: 'Revenue',
          data: [totalRevenue / 4.33, totalRevenue / 4.33, totalRevenue / 4.33, totalRevenue / 4.33],
          borderColor: 'rgba(75, 192, 192, 1)',
          borderWidth: 1,
        },
        {
          label: 'Costs',
          data: [totalOperationalCosts / 4.33, totalOperationalCosts / 4.33, totalOperationalCosts / 4.33, totalOperationalCosts / 4.33],
          borderColor: 'rgba(255, 99, 132, 1)',
          borderWidth: 1,
        },
        {
          label: 'Profit',
          data: [(totalRevenue - totalOperationalCosts) / 4.33, (totalRevenue - totalOperationalCosts) / 4.33, (totalRevenue - totalOperationalCosts) / 4.33, (totalRevenue - totalOperationalCosts) / 4.33],
          borderColor: 'rgba(54, 162, 235, 1)',
          borderWidth: 1,
        },
      ],
      breakEvenMonths,
      breakEvenWeeks,
      individualSessionCustomersNeeded,
      subscriptionCustomersNeeded,
      contractCustomersNeeded,
    };
  };

  const data = calculateResults();

  return (
    <div className="mt-4">
      <h2 className="text-xl font-bold">Results</h2>
      <div className="mt-4">
        <Line data={data} />
      </div>
      <div className="mt-4">
        <h2 className="text-lg font-bold">Break-Even Analysis</h2>
        <p>Break-Even Time: {Math.ceil(data.breakEvenMonths)} months ({Math.ceil(data.breakEvenWeeks)} weeks)</p>
        <p>Individual Session Customers Needed for Break-Even: {data.individualSessionCustomersNeeded.toFixed(2)}</p>
        <p>Subscription Customers Needed for Break-Even: {data.subscriptionCustomersNeeded.toFixed(2)}</p>
        <p>Contract Customers Needed for Break-Even: {data.contractCustomersNeeded.toFixed(2)}</p>
      </div>
    </div>
  );
};

export default Results;
