import React, { useState, useEffect } from 'react';

const InputForm = ({ onChange }) => {
  const [subscriptionsEnabled, setSubscriptionsEnabled] = useState(false);
  const [contractsEnabled, setContractsEnabled] = useState(false);

  const [initialInvestment, setInitialInvestment] = useState({
    vehicle: 0,
    treadmills: 0,
    branding: 0,
    website: 0,
    bookingSoftware: 0,
    other: 0,
  });

  const [operationalCosts, setOperationalCosts] = useState({
    fuel: 0,
    labor: 0,
    insurance: 0,
    maintenance: 0,
    software: 0,
    vehicleLoan: 0,
    otherLoans: 0,
  });

  const [revenueStreams, setRevenueStreams] = useState({
    individualSessionPrice: 0,
    individualSessionCustomers: 0,
    subscriptionsEnabled: false,
    subscriptionPrice: 0,
    subscriptionCustomers: 0,
    subscriptionSessions: 0,
    subscriptionSessionCost: 0,
    contractsEnabled: false,
    contractPrice: 0,
    contractCustomers: 0,
  });

  const handleInitialInvestmentChange = (key, value) => {
    const updatedValues = { ...initialInvestment, [key]: value };
    setInitialInvestment(updatedValues);
    onChange('initialInvestment', updatedValues);
  };

  const handleChange = (section, key, value) => {
    let updatedValues;
    if (section === 'operationalCosts') {
      updatedValues = { ...operationalCosts, [key]: value };
      setOperationalCosts(updatedValues);
    } else if (section === 'revenueStreams') {
      updatedValues = { ...revenueStreams, [key]: value };
      setRevenueStreams(updatedValues);
    }
    onChange(section, updatedValues);
  };

  useEffect(() => {
    const totalInitialInvestment = Object.values(initialInvestment).reduce((a, b) => parseFloat(a) + parseFloat(b), 0);
    onChange('totalInitialInvestment', totalInitialInvestment);
  }, [initialInvestment]);

  return (
    <div className="space-y-4">
      <div>
        <h2 className="text-xl font-bold">Initial Investment</h2>
        <input type="number" placeholder="Vehicle" onChange={(e) => handleInitialInvestmentChange('vehicle', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Treadmills" onChange={(e) => handleInitialInvestmentChange('treadmills', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Branding" onChange={(e) => handleInitialInvestmentChange('branding', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Website" onChange={(e) => handleInitialInvestmentChange('website', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Booking Software" onChange={(e) => handleInitialInvestmentChange('bookingSoftware', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Other" onChange={(e) => handleInitialInvestmentChange('other', e.target.value)} className="border p-2 w-full"/>
        <div className="mt-2">
          <span className="font-bold">Total Initial Investment: </span>${initialInvestment.vehicle + initialInvestment.treadmills + initialInvestment.branding + initialInvestment.website + initialInvestment.bookingSoftware + initialInvestment.other}
        </div>
      </div>
      <div>
        <h2 className="text-xl font-bold">Estimated Monthly Operational Cost</h2>
        <input type="number" placeholder="Fuel" onChange={(e) => handleChange('operationalCosts', 'fuel', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Labor" onChange={(e) => handleChange('operationalCosts', 'labor', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Insurance" onChange={(e) => handleChange('operationalCosts', 'insurance', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Maintenance" onChange={(e) => handleChange('operationalCosts', 'maintenance', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Software" onChange={(e) => handleChange('operationalCosts', 'software', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Vehicle Loan" onChange={(e) => handleChange('operationalCosts', 'vehicleLoan', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Other Loans" onChange={(e) => handleChange('operationalCosts', 'otherLoans', e.target.value)} className="border p-2 w-full"/>
      </div>
      <div>
        <h2 className="text-xl font-bold">Revenue Streams</h2>
        <h3 className="text-lg font-bold">Individual Sessions</h3>
        <input type="number" placeholder="Price per Session" onChange={(e) => handleChange('revenueStreams', 'individualSessionPrice', e.target.value)} className="border p-2 w-full"/>
        <input type="number" placeholder="Estimated Number of Customers" onChange={(e) => handleChange('revenueStreams', 'individualSessionCustomers', e.target.value)} className="border p-2 w-full"/>
        <div>
          <h3 className="text-lg font-bold">Subscriptions</h3>
          <input type="number" placeholder="Price per Subscription" onChange={(e) => handleChange('revenueStreams', 'subscriptionPrice', e.target.value)} className="border p-2 w-full"/>
          <input type="number" placeholder="Estimated Number of Subscriptions" onChange={(e) => handleChange('revenueStreams', 'subscriptionCustomers', e.target.value)} className="border p-2 w-full"/>
          <input type="number" placeholder="Average Number of Sessions per Subscription" onChange={(e) => handleChange('revenueStreams', 'subscriptionSessions', e.target.value)} className="border p-2 w-full"/>
          <input type="number" placeholder="Avg Session Cost with Subscription" onChange={(e) => handleChange('revenueStreams', 'subscriptionSessionCost', e.target.value)} className="border p-2 w-full"/>
          <label className="block">
            <input type="checkbox" onChange={(e) => { handleChange('revenueStreams', 'subscriptionsEnabled', e.target.checked); setSubscriptionsEnabled(e.target.checked); }} className="mr-2"/>
            Enable Subscriptions
          </label>
        </div>
        <div>
          <h3 className="text-lg font-bold">Contracts</h3>
          <input type="number" placeholder="Price per Contract" onChange={(e) => handleChange('revenueStreams', 'contractPrice', e.target.value)} className="border p-2 w-full"/>
          <input type="number" placeholder="Estimated Number of Contracts" onChange={(e) => handleChange('revenueStreams', 'contractCustomers', e.target.value)} className="border p-2 w-full"/>
          <label className="block">
            <input type="checkbox" onChange={(e) => { handleChange('revenueStreams', 'contractsEnabled', e.target.checked); setContractsEnabled(e.target.checked); }} className="mr-2"/>
            Enable Contracts
          </label>
        </div>
      </div>
    </div>
  );
};

export default InputForm;


